.header {
  max-width: 100%;
  min-height: 73px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 45px 0 0;
  border-bottom: 1px solid rgba(84, 84, 84, 0.7);
}

@media (max-width: 618px) {
  .header {
    min-height: 55px;
    padding: 28px 0 0;
  }
}
